<template>
  <div class="center mt-5">
    <div>
      <div>
        <div class=" mb-2 text-lg font-medium px-2">
          <h1>Filtros</h1>

          <div class=" w-1/12 flex items-center">
            <span for="">
              <vs-radio v-model="limite" val="true">10</vs-radio>
            </span>

            <span for="">
              <vs-radio v-model="limite" val="false">Todos</vs-radio>
            </span>
          </div>
        </div>

        <span class=""
          >Exonerados: <b>{{ totalExonerados }}</b></span
        >

        <span class="ml-2">
          Asociados: <b>{{ totalAsociados }}</b></span
        >

        <span class="ml-2">
          Donaciones: <b>{{ totalDonaciones }}</b></span
        >

        <span class="ml-2">
          Normales: <b>{{ totalNormales }}</b></span
        >

        <span class="ml-5 text-xl font-bold p-2 bg-black rounded-xl text-white"
          >Total: <b>{{ payments.length }}</b></span
        >
      </div>

      <div>
        <span class="text-red-500"
          >Total Repetidos: <b>{{ countExist }}</b></span
        >

        <span class=" ml-2 text-green-500"
          >Total Registrados: <b>{{ count_registrados }}</b></span
        >
      </div>
      <span class=" text-xs text-gray-400"
        >Nota: Los registros con número de operación repetida se omiten al
        momento de guardar los datos.</span
      >
    </div>

    <vs-table striped class=" mt-5">
      <template #header>
        <div class=" flex">
          <vs-input v-model="search" border placeholder="Dni" />
          <vs-button class=" focus:outline-none" @click="eventSearch(search)"
            >Buscar</vs-button
          >
        </div>
      </template>
      <template #thead>
        <vs-tr>
          <vs-th sort @click="payments = $vs.sortData($event, payments, 'id')">
            Id
          </vs-th>

          <vs-th
            sort
            @click="payments = $vs.sortData($event, payments, 'nombre')"
          >
            Nombre
          </vs-th>
          <vs-th
            sort
            @click="payments = $vs.sortData($event, payments, 'num_doc')"
          >
            Numero Doc.
          </vs-th>
          <vs-th
            sort
            @click="payments = $vs.sortData($event, payments, 'fecha')"
          >
            Fecha
          </vs-th>

          <vs-th
            sort
            @click="payments = $vs.sortData($event, payments, 'monto')"
          >
            Monto
          </vs-th>

          <vs-th
            sort
            @click="payments = $vs.sortData($event, payments, 'num_operacion')"
          >
            Num. Operación
          </vs-th>

          <vs-th
            sort
            @click="
              payments = $vs.sortData($event, payments, 'fecha_inscripcion')
            "
          >
            Inscrito
          </vs-th>

          <vs-th
            sort
            @click="payments = $vs.sortData($event, payments, 'is_exonerado')"
          >
            Es Exonerado
          </vs-th>

          <vs-th
            sort
            @click="payments = $vs.sortData($event, payments, 'asociado')"
          >
            Es Asociado
          </vs-th>

          <vs-th
            sort
            @click="payments = $vs.sortData($event, payments, 'is_donacion')"
          >
            Es Donación
          </vs-th>

          <vs-th
            sort
            @click="payments = $vs.sortData($event, payments, 'sede')"
          >
            Lugar de Registro
          </vs-th>
        </vs-tr>
      </template>
      <template #tbody>
        <vs-tr :key="i" v-for="(tr, i) in payments" :data="tr">
          <vs-td class=" font-semibold" edit @click="$emit('edit', tr)">
            {{ tr.id }}
          </vs-td>

          <vs-td class=" font-semibold" edit @click="$emit('edit', tr)">
            {{ tr.nombre }}
          </vs-td>
          <vs-td>
            {{ tr.num_doc }}
          </vs-td>
          <vs-td>
            {{ tr.fecha }}
          </vs-td>

          <vs-td class=" font-bold"> S/.{{ tr.monto | parseMoney }} </vs-td>

          <vs-td>
            <span :class="{ 'bg-red-200 px-5': tr.repetido }">
              {{ tr.num_operacion }}
            </span>
          </vs-td>

          <vs-td>
            <span
              :class="{ 'bg-green-500 px-5 text-white': tr.fecha_inscripcion }"
            >
              {{ tr.fecha_inscripcion ? "Inscrito" : "" }}
            </span>
          </vs-td>

          <vs-td>
            <span :class="{ 'bg-green-200 px-5': tr.is_exonerado }">
              {{ tr.is_exonerado ? "Si" : "No" }}
            </span>
          </vs-td>

          <vs-td>
            <span :class="{ 'bg-green-200 px-5': tr.asociado }">
              {{ tr.asociado ? "Si" : "No" }}
            </span>
          </vs-td>

          <vs-td>
            <span :class="{ 'bg-green-200 px-5': tr.is_donacion }">
              {{ tr.is_donacion ? "Si" : "No" }}
            </span>
          </vs-td>

          <vs-td>
            <span class=" uppercase">
              {{ tr.sede || "iquitos" }}
            </span>
          </vs-td>
        </vs-tr>
      </template>

      <template #notFound>
        No existen datos 😢
      </template>
    </vs-table>
  </div>
</template>
<script>
export default {
  props: {
    dataList: {
      type: Array,
      default: () => {
        return {};
      },
    },

    countExist: {
      type: Number,
      default: 0,
    },

    actualizar: {
      type: Boolean,
      default: false,
    },
  },

  data: () => ({
    search: "",
    payments: [],
    limite: "true",
    isSede: false,
  }),

  computed: {
    count_registrados() {
      return this.payments.filter((item) => item.fecha_inscripcion).length;
    },

    get_pago() {
      return this.$store.getters["pago/get_pago"];
    },

    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },

    SedeText() {
      if (this.get_auth.group == "sede") return this.get_auth.username;
      return "";
    },

    totalExonerados() {
      return this.payments.filter((item) => item.is_exonerado).length;
    },

    totalAsociados() {
      return this.payments.filter((item) => item.asociado).length;
    },

    totalDonaciones() {
      return this.payments.filter((item) => item.is_donacion).length;
    },

    totalNormales() {
      return this.payments.filter(
        (item) => !item.is_donacion && !item.asociado && !item.is_exonerado
      ).length;
    },
  },

  watch: {
    actualizar(newValue) {
      if (newValue) {
        this.$store.dispatch("pago/set_pago", {
          dni: this.search,
          limit: this.limite,
          sede: this.SedeText,
        });
        this.$emit("actualizado");
      }
    },

    limite(newValue) {
      this.$store.dispatch("pago/set_pago", {
        dni: this.search,
        limit: newValue,
        sede: this.SedeText,
      });
    },

    dataList(newValue) {
      this.payments = newValue;
    },

    get_pago(newValue) {
      this.payments = newValue;
    },

    search(newValue) {
      const buscar = newValue.length;
      if (buscar == 0 || buscar == 4 || buscar == 8) {
        this.$store.dispatch("pago/set_pago", {
          dni: newValue,
          limit: this.limite,
          sede: this.SedeText,
        });
      }
    },
  },

  methods: {
    eventSearch(newValue) {
      this.$store.dispatch("pago/set_pago", {
        dni: this.search,
        limit: newValue,
        sede: this.SedeText,
      });
    },
  },
};
</script>
